@use '../abstracts' as a;

.hero-section {
	background: a.$clr-primary-color;
	padding: 10em 3em;
	position: relative;
	z-index: 1;

	.hero-wrapper {
		display: flex;
		flex-direction: column;
	}
	h2 {
		display: flex;
		color: a.$white;
		font-family: a.$MainFont;
		font-size: 1.5rem;

		span {
			color: a.$clr-third;
		}

		.Typist .Cursor {
			display: inline-block;
			margin-left: 0.2em;

			&--blinking {
				opacity: 1;
				animation: blink 1s linear infinite;
				@keyframes blink {
					0% {
						opacity: 1;
					}
					50% {
						opacity: 0;
					}
					100% {
						opacity: 1;
					}
				}
			}
		}

		.Mytypist {
			display: flex;
			margin-left: 0.22em;
		}
	}

	h3 {
		text-transform: uppercase;
		font-family: a.$MainFont;
		font-weight: lighter;
		letter-spacing: 0.3em;
		color: a.clr(menu, dark);

		font-size: 1rem;
	}

	pre {
		color: a.$white;
		font-family: a.$MainFont;
		font-weight: bold;
		text-transform: uppercase;
		font-size: 3rem;
		line-height: 1.2em;
	}

	.cta-btn {
		@extend %Button;
	}
}
