.headroom {
	background: a.$clr-primary-color;
	padding: 1.5em 3em;
	top: 0;
	left: 0;
	right: 0;
	z-index: 99;

	&--unfixed {
		position: relative;
		transform: translateY(0);
	}
	&--scrolled {
		transition: transform 400ms ease-in-out;
	}
	&--unpinned {
		position: fixed;
		transform: translateY(-100%);
	}
	&--pinned {
		background: a.$clr-primary-color;
		box-shadow: inset 0px 10px 250px rgba(0, 0, 0, 0.25);
		position: fixed;
		transform: translateY(0%);
	}

	h1 {
		font-size: 2.2rem;
		font-family: a.$MainFont;
		color: a.$white;

		span {
			font-family: a.$MainFont;
			color: a.$clr-third;
		}
	}
	.toggled {
		content: "";
		position: absolute;
		top: -150px;
		right: 0;
		z-index: 99;
		height: 70vh;
		width: 70vw;
		background: a.$clr-secondary;
		transition: all 1s ease;
		transform: translate(-120%, -120%) rotate(360deg);
	}

	.fade-out {
		display: flex;
		flex-direction: column;
		padding: 1em;
		border-radius: 0.5em;
		box-shadow: 5px 5px 5px 1px a.$clr-secondary;
		justify-content: space-between;
		transition: all 10s ease;
		content: "";
		position: absolute;
		top: 77px;
		right: 0;
		z-index: 99;
		height: 50vh;
		width: 50vw;
		background: a.$clr-secondary;
	}

	.menu-list {
		display: flex;
		flex-flow: column wrap;
		justify-content: space-evenly;
		align-content: center;

		li,
		a {
			text-decoration: none;
			list-style: none;
		}

		li a {
			font-size: 1.5rem;
			color: a.$white;

			&:hover {
				font-weight: bold;
				color: a.$clr-third;
			}
		}

		li {
			margin: 0.5em auto;
		}
	}
	nav {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
		align-items: center;
	}

	.social-media {
		display: flex;
		flex-direction: row;

		justify-content: center;
		img {
			width: 2em;
			height: 2em;
			margin: 0 0.5em;
		}
	}
}

.ham-menu {
	width: 1.5em;
	height: 1.5em;
	color: a.clr(menu, dark);
	cursor: pointer;
}

.exit-ham {
	width: 1.5em;
	height: 1.5em;
	animation: shift-menu 1s ease;
	color: a.$white;
	cursor: pointer;
}

@keyframes shift-menu {
	0% {
		transform: rotate(360deg);
	}
}
