@use '../abstracts' as a;

.projects-section {
	background: a.$clr-third;
	box-shadow: a.$shadow;
	padding: 3em 3em;

	.projects-wrapper {
		outline: 1px solid red;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: repeat(3, 1fr);
		gap: 0.5em;

		.portfolio-wrapper {
			grid-column: 1 / span 3;
			@extend %Card;
			.card-picture {
				@extend %Picture;
			}
			p {
				max-width: 34ch;
			}
			h1 {
				margin-bottom: 1em;
			}
			.card-button {
				@extend %Button;
			}
		}

		.database-wrapper {
			grid-column: 1 / span 3;
			@extend %Card;
			.card-picture {
				@extend %Picture;
			}
			p {
				max-width: 34ch;
			}
			h1 {
				margin-bottom: 1em;
			}
			.card-button {
				@extend %Button;
			}
		}
		.crud-wrapper {
			grid-column: 1 / span 3;
			@extend %Card;
			.card-picture {
				@extend %Picture;
			}
			p {
				max-width: 34ch;
			}
			h1 {
				margin-bottom: 1em;
			}
			.card-button {
				@extend %Button;
			}
		}
	}
	.subject-title-wrapper {
		display: flex;
		justify-content: center;
		.subject-title-projects {
			@include a.Title(a.$clr-primary-color);
		}
	}
}
