@use './colors' as a;
@use './fonts' as f;

%Picture {
	background: gray;
	border-radius: 1em;
	margin-top: -1.5em;
	margin-bottom: 3em;
	border: 1px solid a.$clr-third;
}
%Card {
	display: flex;
	flex-direction: column;
	align-items: center;

	position: relative;
	z-index: 1;
	padding: 1em;
	border-radius: 0.5em;
	background: a.$clr-primary-color;
}
%Button {
	position: relative;
	padding-left: 0.5em;
	margin-top: 0.5em;
	border: none;
	background: none;
	font-size: 1rem;
	color: a.$white;
	cursor: pointer;

	&::after {
		position: absolute;
		border-radius: 0.1em;
		content: "";
		top: 10px;
		right: 5px;
		left: 1px;
		bottom: -3px;
		background: a.$clr-third;
		z-index: -1;
		transform-origin: bottom left;
		transition: transform 175ms cubic-bezier(0.91, 0.01, 0.67, 1.72);
	}

	&:hover::after,
	&:focus::after {
		transform: scale(1.22, 1.95);
	}
}

%Wheel {
	display: none;
	width: 50px;
	height: 50px;
	object-fit: cover;
	border-radius: 0.5em;
	//box-shadow: inset 5px 5px 10px 2px rgba(0, 0, 0, 0.25);
}
