@use './colors' as a;
@use './fonts' as f;
@mixin animation() // fade out back in
 {
	animation: fadeInAnimation ease 3s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
	@keyframes fadeInAnimation {
		0% {
			opacity: 0;
		}
		100% {
			opacity: 1;
		}
	}
}

@mixin Title($clr-underline) {
	text-align: center;
	color: a.$white;
	margin-bottom: 3em;
	position: relative;
	font-size: 1.5rem;
	font-family: f.$MainFont;
	font-weight: bold;

	&::after {
		position: absolute;
		content: "";
		top: 43px;
		bottom: -10px;
		right: 0;
		left: 0;

		background: $clr-underline;
	}
}
