@use '../abstracts' as a;
.about-section {
	background: a.$clr-secondary;
	box-shadow: a.$shadow;
	padding: 3em 3em;

	.scroll-wheel {
		text-align: end;

		.wheel {
			@extend %Wheel;
		}
	}
	.subject-title-wrapper {
		display: flex;
		justify-content: center;
		.subject-title-about {
			@include a.Title(a.$clr-third);
		}
	}

	.about-wrapper {
		.about-illustration {
			margin-bottom: 2em;
			.illustration-img {
				width: 250px;
				height: 250px;
			}
		}
		.description {
			display: flex;
			flex-direction: column;

			h1 {
				margin-bottom: 0.5em;
				font-size: 1.35rem;
			}
			p {
				max-width: 50ch;
				line-height: 1.5em;
			}

			.cv-button {
				@extend %Button;
			}
		}
	}
}
