$clr-primary-color: #1f1f1f;
$clr-secondary: #3b3b3b;
$clr-third: #ff7600;
$shadow: inset 0px 10px 250px rgba(0, 0, 0, 0.25);

$youtube: #d16e8d;
$articles: #ffac83;
$community: #3f78c9;
$courses: #49c4a3;
$white: #fff;
$black: #212427;
$clr-icons: #929292;
$clr-text: #fff;

$sideMenuUnderline: linear-gradient(
	90deg,
	rgba(2, 0, 36, 1) 0%,
	rgba(9, 9, 121, 1) 39%,
	rgba(1, 66, 214, 1) 100%,
	rgba(0, 212, 255, 1) 100%
);
$lighten: 5;
$darken: 25;

$colors: (
	primary: (
		base: $clr-primary-color,
		light: lighten($clr-primary-color, $lighten),
		dark: darken($clr-primary-color, $darken),
	),
	yt: (
		base: $youtube,
		light: lighten($youtube, $lighten),
		dark: darken($youtube, $darken),
	),
	articles: (
		base: $articles,
		light: lighten($articles, $lighten),
		dark: darken($articles, $darken),
	),
	com: (
		base: $community,
		light: lighten($community, $lighten),
		dark: darken($community, $darken),
	),
	course: (
		base: $courses,
		light: lighten($courses, $lighten),
		dark: darken($courses, $darken),
	),
	menu: (
		base: $white,
		light: lighten($white, $lighten),
		dark: darken($white, $darken),
	),
);

@function clr($base, $shade: base) {
	$color: map-get(map-get($colors, $base), $shade);
	@return $color;
}
